import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import { FaReact } from 'react-icons/fa';


function Nav() {

    const [burgerIcon, setBurgerIcon] = useState(true)
    const [mystyle, setMystyle] = useState()

    const navStyle = {
        color: 'white',
    }

    // window.addEventListener('click', function (e) {
    //     if (document.getElementById('clickbox').contains(e.target)) {
    //         console.log("ASD")
    //     } else {
    //         if (burgerIcon) {
    //             console.log("X")
    //             navSlide()
    //             setMystyle({
    //                 transform: "translateX(100%)",
    //             })
    //         }
    //     }
    // })

    // const navSlide = () => {
    //     let burger = document.querySelector('.burger')
    //     burger.classList.toggle("toggle");
    // }

    function changeBurger() {
        // navSlide()
        setBurgerIcon(!burgerIcon)

        if (burgerIcon) {
            setMystyle({
                transform: "translateX(0%)",
            })
        }
        else {
            setMystyle({
                transform: "translateX(100%)",
            })
        }
    }

    function close() {
        setMystyle({
            transform: "translateX(100%)",
        })
        setBurgerIcon(!burgerIcon)
        console.log("xxx")
    }

    function outClick() {
        // if (!burgerIcon) {
        //     setMystyle({
        //         transform: "translateX(100%)",
        //     })
        // }
    }

    return (
        <nav id="clickbox" className="navApp" onClick={outClick}>
            <Link style={navStyle} to='/'>
                <div>
                    <h3 className="logoIcon"> React APP</h3>
                    <FaReact className="testt" />
                </div>
            </Link>
            <ul className="navLinks" style={mystyle} onClick={() => close()} >
                <Link style={navStyle} to='/IsometricCSS/IsometricCSS'>
                    <li>isometric CSS</li>
                </Link>
                <Link style={navStyle} to='/Currency/CurrencyApp'>
                    <li>Prevod</li>
                </Link>
                <Link style={navStyle} to='/TicTacToe/TicTacToe'>
                    <li>TicTacToe</li>
                </Link>
                <Link style={navStyle} to='/Todo/TodoApp'>
                    <li>Todo</li>
                </Link>
                <Link style={navStyle} to='/Recepty/Recepty'>
                    <li>Recepty</li>
                </Link>
                <Link style={navStyle} to='/ReceptyV2/ReceptyV2'>
                    <li>ReceptyV2</li>
                </Link>
                <Link style={navStyle} to='/ExpenseTracker/ExpenseTrackerApp'>
                    <li>Expense Tracker</li>
                </Link>
                <Link style={navStyle} to='/Covid/CovidApp'>
                    <li>Covid - 19</li>
                </Link>
            </ul>

            <div className="burger" onClick={changeBurger}>
                <div className="line1" ></div>
                <div className="line2" ></div>
                <div className="line3"></div>
            </div>
        </nav>
    )
}

export default Nav
